<template>
  <div class="debug">
    <div class="debug__container">
      <div class="close-icon" @click="switchDebug" />

      <h3></h3>
      <table class="table">
        <thead>
          <tr class="table-title">
            <th></th>
            <th></th>
            <th>kb/s</th>
            <th>kb/s</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{ mimeType }}</td>
            <td>{{ time || 0 }}</td>
            <td>{{ bytesReceivedSecond }}</td>
            <td>{{ bytesSentSecond }}</td>
          </tr>
        </tbody>
      </table>

      <br />
      <h3></h3>
      <table class="table">
        <thead>
          <tr class="table-title">
            <th></th>
            <th></th>
            <th>kb/s</th>
            <th>kb/s</th>
            <th>/s</th>
            <th>/s</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in sender" :key="key">
            <td></td>
            <td>{{ item.frameWidth }}*{{ item.frameHeight }}</td>
            <td>{{ item.expBandwidth }}</td>
            <td>{{ item.bytesSentSecond }}</td>
            <td>{{ item.framesEncodedSecond }}</td>
            <td>{{ item.framesSentSecond }}</td>
            <td>{{ item.keyFramesEncoded }}</td>
          </tr>
        </tbody>
      </table>
      <br />

      <h3></h3>
      <table class="table">
        <thead>
          <tr class="table-title">
            <th></th>
            <th>Codec</th>
            <th>(kbps)</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in audioSender" :key="key">
            <td></td>
            <td>{{ item.mimeType }}</td>
            <td>{{ item.bytesSentSecond }}</td>
            <td>{{ item.audioLevel }}</td>
          </tr>

          <tr v-for="(item, key) in audioReceiver" :key="key">
            <td></td>
            <td>{{ item.mimeType }}</td>
            <td>{{ item.bytesReceivedSecond }}</td>
            <td>{{ item.audioLevel }}</td>
          </tr>
        </tbody>
      </table>
      <br />

      <br />
      <h3></h3>
      <table class="table">
        <thead>
          <tr class="table-title">
            <th></th>
            <th></th>
            <th></th>
            <th>/s</th>
            <th>/s</th>
            <th>kb/s</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="({
              frameWidth,
              frameHeight,
              bytesReceivedSecond,
              framesReceivedSecond,
              framesDecodedSecond,
              type,
              name,
              isContent,
              keyFramesDecoded,
            },
            key) in receiver"
            :key="key"
          >
            <td>{{ name }}</td>
            <td>{{ type }} * {{ isContent ? "Con" : "Peo" }}</td>
            <td>{{ frameWidth }}*{{ frameHeight }}</td>
            <td>{{ framesDecodedSecond }}</td>
            <td>{{ framesReceivedSecond }}</td>
            <td>{{ bytesReceivedSecond }}</td>
            <td>{{ keyFramesDecoded }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import { transformTime } from "@/utils/index";
export default {
  props: ["senderStatus"],
  computed: {
    time() {
      return transformTime(this.timestamp);
    },
  },
  data() {
    const {
      mimeType,
      sender = {},
      timestamp,
      receiver = {},
      bytesReceivedSecond,
      bytesSentSecond,
      audioSender = {},
      audioReceiver = {},
    } = this.senderStatus;
    return {
      mimeType,
      sender,
      timestamp,
      receiver,
      bytesReceivedSecond,
      bytesSentSecond,
      audioSender,
      audioReceiver,
    };
  },
  methods: {
    switchDebug() {
      this.$emit("switchDebug");
    },
  },
  watch: {
    senderStatus: {
      handler(newValue) {
        const {
          mimeType,
          sender = {},
          timestamp,
          receiver = {},
          bytesReceivedSecond,
          bytesSentSecond,
        } = newValue;
        this.mimeType = mimeType;
        this.sender = sender;
        this.timestamp = timestamp;
        this.receiver = receiver;
        this.bytesReceivedSecond = bytesReceivedSecond;
        this.bytesSentSecond = bytesSentSecond;
      },
    },
  },
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
